<template>
  <div>
    <top></top>
    <el-carousel height="350px" style="width:100%" :interval="5000" arrow="always">
      <el-carousel-item v-for="item in banner" :key="item">
        <img :src="item.url" style="height:350px;width:100%">
      </el-carousel-item>
    </el-carousel>
    <div class="box mar">
      <div class="filterBoxWrap">
      <div class="headTipsBox">
        <img class="goods_type_img" src="../../static/goods_type.png"/>
        <span>商品分类</span>
      </div>
        <ul class="filterRow" >
          <li v-for="item in 8">
            <a class="linkBox" @click="toShop(item)">
              <div class="tagNameDetail">
                <strong>{{goodsType[item-1]}}</strong>
                <span>{{goodsTypeDetail[item-1]}}</span>
              </div>
              <div class="goTag">
                <div :class="'tagGo color_type_'+(item-1)">
                  <span >GO ></span>
                  <i class="iconfont iconright"></i>
                </div>
              </div>
            </a>
          </li>
        </ul>
      </div>
    </div>
    <div class="box mar">
      <!--<el-row :gutter="20">-->
        <!--<el-col :span="6">-->
          <!--<div class="indexFour">-->
            <!--<img src="../../static/indexFour1.png">-->
          <!--</div>-->
        <!--</el-col>-->
        <!--<el-col :span="6">-->
          <!--<div class="indexFour">-->
            <!--<img src="../../static/indexFour2.png">-->
          <!--</div>-->
        <!--</el-col>-->
        <!--<el-col :span="6">-->
          <!--<div class="indexFour">-->
            <!--<img src="../../static/indexFour3.png">-->
          <!--</div>-->
        <!--</el-col>-->
        <!--<el-col :span="6">-->
          <!--<div class="indexFour">-->
            <!--<img src="../../static/indexFour4.png">-->
          <!--</div>-->
        <!--</el-col>-->
      <!--</el-row>-->
      <div class="indexList mar " style="background: #F4F4F4">
        <div class="indexListTitle">
          <img class="fontTitle" src="../../static/hot_goods.png"/>
          <span class="fontDesc">多种商品满足你的需求</span>
        </div>
        <div style="display: flex;flex-direction: row">
          <img class="hot_icon" src="../../static/day_goods.png"/>
        <div class="indexlist">
          <li v-for="(item, index) in list" :key="index" @click="$router.push({path:'/member'})">
            <img :src="item.images.split(',')[0]">
            <div class="indexlistTitle">
              <span class="price" style="float: right;font-size: 16px; color: #ff4d70;">¥
                <span class="money">{{item.price / 100}}元</span>
              </span>
              <div class="name">{{item.name}}</div>
              <span class="goods_weight">重量：<span style="color: #9C0321">{{item.weight/1000}}kg/件</span></span>
            </div>
            <!-- <el-button @click="$router.push({path:'/member'})" type="primary">立即购买</el-button> -->
          </li>
        </div>
        </div>
      </div>
      <div class="indexList mar fffbg">
        <div class="indexListTitle">
          <img class="fontTitle" src="../../static/new_goods.png"/>
          <span class="fontDesc">我们上新啦</span>
        </div>
        <div class="indexlist">
          <li v-for="(item, index) in list2" style="width: 19%;margin-bottom: 0" :key="index" @click="$router.push({path:'/member'})">
            <img :src="item.images.split(',')[0]">
            <div class="indexlistTitle" style="color: #999999">
              <div class="name">{{item.name}}</div>
              <span class="price" style="float: right;display: flex">
                <span class="money" style="font-size: 16px">¥ {{item.price / 100}}元</span>
                  <span  style="font-size: 16px;margin-left: auto">重量：{{item.weight/1000}}kg/件</span>
              </span>

            </div>
            <!-- <div class="indexlistTitle">{{item.name}}<span style="float: right">单价：{{item.price / 100}}元</span></div> -->
            <!-- <el-button @click="$router.push({path:'/member'})" type="primary">立即购买</el-button> -->
          </li>
        </div>
      </div>
    </div>
    <foot/>
  </div>
</template>

<script>
import foot from "./public/foot.vue";
import top from "./public/top.vue";

export default {
  components: {
    foot,
    top
  },
  data() {
    return {
      banner: [
        {
          url: require("../../static/banner1.jpg")
        },
        {
          url: require("../../static/banner2.jpg")
        },
        {
          url: require("../../static/banner3.jpg")
        }
      ],
      list: [],
      list2: [],

      goodsType:['一毛专区','新品专区','配重专区','高端精品',
        '畅销专区','厨卫用品','办公用具','家具日常'],
      goodsTypeDetail:['比你在便宜一点儿','精彩生活一点新','合理配置轻松下单','为你找到最合适的',
        '热销单品轻松购物','打造梦想中的生活','今天也要努力工作哦','选你喜欢的生活方式'],
    };
  },
  mounted() {
    this.getList();
    this.getList2();
    const { inviteCode } = this.$route.query;
    localStorage.setItem("inviteCodeParent", inviteCode);
    sessionStorage.setItem("inviteCodeParent", inviteCode);
    // this.getNotice();
  },
  methods: {
    toShop(item){
      this.$router.push({path:'/shop',query:{id:item+''}});
    },
    getNotice() {
      this.axios.get("/giftmall/notice").then(res => {
      });
    },
    getList() {
      let that = this;
      this.axios
        .get(
          // process.env.API_HOST+"goods?name=&state=0&page=0&size=5&goodBrand=true",
          "/giftmall/goods?name=&state=0&page=0&size=8&goodBrand=true",
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao1.tianyalei.com",
              domain: this.url,
              Authorization: sessionStorage.getItem("token")
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
            that.list = response.data.data.list;
          }
        });
    },
    getList2() {
      let that = this;
      this.axios
        .get(
          // process.env.API_HOST+"goods?name=&state=0&page=0&size=5&newGoods=true",
          "/giftmall/goods?name=&state=0&page=0&size=5&newGoods=true",
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao1.tianyalei.com",
              domain: this.url,
              Authorization: sessionStorage.getItem("token")
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
            that.list2 = response.data.data.list;
          }
        });
    }
  }
};
</script>

<style scoped>
.fffbg {
  background: #fff;
}
.indexFour {
  text-align: center;
}
.indexFour img {
  width: 100%;
}
.mar {
  margin-top: 20px;
}
.hot_icon{
  width: 240px;
  height: 756px;
}
.indexListTitle {
  border-bottom: 1px solid #eaeaea;
  height: 60px;
  display: flex;
  line-height: 60px;
  vertical-align: middle;
}
.sx {
  background: #ec3440;
  height: 30px;
  width: 5px;
  border-radius: 3px;
  float: left;
  margin-left: 15px;
  margin-right: 10px;
  margin-top: 15px;
}
.fontTitle {
  font-size: 24px;
  float: left;
  height: 24px;
  margin:auto 12px;
}
.fontDesc {
  font-size: 16px;
  color: #ccc;
}
.indexlist {
  overflow: hidden;
}

.indexlist img {
  width: 100%;
}
.indexlistTitle {
  line-height: 25px;
  font-size: 16px;
  margin-top: 5px;
  margin-bottom: 10px;
}

</style>

<style scoped lang="less">
  .color_type_0{
    border:solid 2px #FD1B1B;
    color: #FD1B1B;
  }
  .color_type_1{
    border:solid 2px #FF9952;
    color: #FF9952;
  }
  .color_type_2{
    border:solid 2px #FFBA23;
    color: #FFBA23;
  }
  .color_type_3{
    border:solid 2px #38D16F;
    color: #38D16F;
  }
  .color_type_4{
    border:solid 2px #0CC7BA;
    color: #0CC7BA;
  }
  .color_type_5{
    border:solid 2px #4AC2FF;
    color: #4AC2FF;
  }
  .color_type_6{
    border:solid 2px #229FFF;
    color: #229FFF;
  }
  .color_type_7{
    border:solid 2px #1D44FF;
    color: #1D44FF;
  }

  .filterBoxWrap{
    display: flex;
    flex-direction: row;
    .filterRow{
      flex: 1;
      display: flex;
      flex-wrap: wrap;
      li{
        display: flex;
        align-items: center;
        height: 68px;
        box-sizing: border-box;
        width: 24.5%;
        border-radius: 4px;
        margin-right: 0.5%;
        margin-bottom: 0.5%;
        padding: 0 12px;
        background: #fff;
        box-shadow: 0 3px 3px rgba(0,0,0,.1);
        .linkBox{
          flex: 1;
          display: flex;
          align-items: center;
          justify-content: space-between;
          cursor: pointer;
          .tagNameDetail{
            display: flex;
            flex-direction: column;
            justify-content: center;
            strong{
              font-size: 18px;
              line-height: 25px;
              color: rgba(0,0,0,.85);
              font-weight: 700;
              margin-bottom: 2px;
            }
            span{
              font-size: 14px;
              line-height: 20px;
              color: rgba(0,0,0,.45);
              font-weight: 400;
            }
          }
          .goTag{
            width: 50px;
            height: 22px;
            box-sizing: border-box;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: row;
            border-width: 2px;
            border-style: solid;
            border-radius: 11px;
            position: relative;
            color: transparent;
            z-index: 0;
            .tagGo{
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              border-radius: 11px;
              background: #fff;
              z-index: 0;
              display: flex;
              justify-content: center;
              align-items: center;
              flex-direction: row;
            }
            span{
              font-size: 14px;
              line-height: 1;
              font-weight: bolder;
              margin-right: 4px;
            }
          }
        }
      }

    }
    .headTipsBox{
      background: #F5A623;
      border-radius: 4px;
      width: 168px;
      height: 145px;
      margin-right: 6px;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      .goods_type_img{
        width: 40px;
        height: 40px;
      }
       span {
        font-size: 16px;
        line-height: 22px;
        color: hsla(0,0%,100%,.85);
        font-weight: 400;}
    }
  }
.indexlist {
  li {
    width: 24%;
    margin: 0 0 25px 1%;
    float: left;
    background: #ffffff;
    box-sizing: border-box;
    border: 3px solid white;
    padding: 5px;
    cursor: pointer;
    border-radius: 5px;
    &:hover {
      border: 3px solid #ff4d70;
      border-radius: 5px;
    }

    .indexlistTitle {
      display: flex;
      flex-direction: column;
      margin: 0;
      .price {
        padding: 10px 0;
        .money {
          color: #ff4d70;
          font-size: 28px;
        }
      }
      .name {
        padding-bottom: 20px;
        font-size: 14px;
      }
      .goods_weight{
        color:#333;
        border-top:1px solid #eaeaea;
        padding-top: 8px;
      }
    }
  }

}

</style>



